@import url('https://fonts.googleapis.com/css2?family=Alata&family=Crimson+Text&family=Montserrat:wght@300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Alata&family=Crimson+Text&family=Montserrat:ital,wght@0,300;1,500&display=swap');

.projectContainer{
    display: flex;
    flex-direction: column;
    color: white;
    justify-content: center;
    margin: 200px 0;
    align-items: center;
}
.projectContainer>h1{
    padding-bottom: 15px;
}
.projectContainer>p{
    padding-bottom: 20px;
}
.projectContainer__projects{
    display: flex;
    width: 80vw;
    margin-top: 50px;
    flex-wrap: wrap;
}

